<template>
    <div id="alertBox" v-show="showAlert" @click.self="onClose">
        <div class="content" v-html="alertHtml"></div>
        <button class="btn btn-primary btn-lg" @click="onClose"> X </button>
    </div>
</template>
<script>
import { mapGetters } from 'vuex';

export default {
    //TODO: 背景被點擊時需阻止冒泡
    computed: {
        ...mapGetters(['showAlert', 'alertHtml'])
    },
    data() {
        return {
            msg: "<p>素材資料來源: http://makapri.web.fc2.com/top.html </p>背景圖片資料來源: https://indienova.com/resource/r/xcfgdtkjbjcountry-platform-tiles"
        }
    },
    methods: {
        onClose() {
            this.$store.commit('SHOW_ALERT', false);
        }
    }
}
</script>
<style lang="less" scoped>
    #alertBox {
        background-color: rgba(0, 0, 0, 0.5);
        position: absolute;
        top: 0;
        left: 0;
        width: 100vw;
        height: 100vh;
        z-index: 99999;
        text-align: center;

        &:after {
            content:'';
            width:0;
            height:100vh;
            display:inline-block;
            vertical-align: middle;
        }
        .content {
            display: inline-block;
            width: 60vw;
            height: 60vh;
            background-color: #FCFCFC;
            border-radius: 20px;
            vertical-align: middle;
            overflow-y: auto;
            overflow-x: hidden;
            word-break: break-all;
            padding: 15pt;

            &:before {
                content: '';
                position: absolute;
                top: 0; 
                right: 0; 
                bottom: 0; 
                left: 0;
                z-index: -1;
                margin: 18vh 18.5vw;
                border-radius: inherit;
                background-image: linear-gradient(-20deg, #fc6076 0%, #ff9a44 100%);
            }
        }
        .btn {
            position: absolute;
            top: 10%;
            right: 10%;
            font-size: 18pt;
        }
    }
</style>