export default {
    SHOW_PAGE_WINDOW(state, boolean) {
        state.showPageWindow = boolean;
    },
    SET_PAGE_URL(state, url) {
        state.pageUrl = url;
    },
    SHOW_ALERT(state, boolean) {
        state.showAlert = boolean;
    },
    SET_ALERT_HTML(state, html) {
        state.alertHtml = html;
    },
    SHOW_LOADING(state, boolean) {
        state.loading = boolean;
    }
}