<template>
    <div class="carousel">
        <div class="carousel__content" :style="contentStyle">
            <section class="box" v-for="(val, key) in list" :key="key"> 
                <label>{{ val.name }}</label>
                <img class="rounded" :src="val.img" />
                <div class="carousel-btn-group">
                    <button class="btn btn_page" @click="showNewWindow(val.url)"> {{ title.newPage }} </button>
                    <!-- <button class="btn btn_page" @click="showPanel(val.url)"> {{ title.localPage }} </button> -->
                </div>
            </section>
        </div>
        <div class="arrowBlock arrowBlock--left" @click="preBox">
            <div class="arrow arrow--left"></div>
        </div>
        <div class="arrowBlock arrowBlock--right" @click="nextBox">
            <div class="arrow arrow--right"></div>
        </div>
    </div>
</template>
<script>
import { mapGetters } from 'vuex';

//TODO: 當demo圖非360deg時, 被點到需轉到360deg(正向)
//TODO: 勇者從左側進場, 並顯示對話框
//TODO: 修改左右兩側換頁樣式

export default {
    data() {
        return {
            title: {
                // newPage: '分頁瀏覽',
                newPage: '瀏覽',
                localPage: '本頁瀏覽'
            },
            list: [
                {name: '音樂app 樣版展示', img: './images/p_1.png', url: 'https://kuikui22.github.io/sideProject_demo/VueJs/animalMusic/'},
                {name: 'slot game', img: './images/p_2.png', url: 'https://kuikui22.github.io/sideProject_demo/CocosCreator/TranSlot_00/'},
                {name: '2D 跑酷', img: './images/p_3.png', url: 'https://kuikui22.github.io/sideProject_demo/CocosCreator/TranRunGame/'},
                {name: 'F2e番茄鐘', img: './images/p_4.png', url: 'https://kuikui22.github.io/sideProject_demo/VueJs/pomodoro_G00/index.html'}
            ],
            rotateAngle: 0,
            contentStyle: {
                transform: 'rotateY(0deg)'
            }
        }
    },
    computed: {
        ...mapGetters(['pageUrl'])
    },
    methods: {
        nextBox() {
            // this.rotateAngle += 120;
            this.rotateAngle += 90;
            this.contentStyle.transform = `rotateY(${this.rotateAngle}deg)`;
        },
        preBox() {
            // this.rotateAngle -= 120;
            this.rotateAngle -= 90;
            this.contentStyle.transform = `rotateY(${this.rotateAngle}deg)`;
        },
        showPanel(url) {
            this.$store.commit('SHOW_PAGE_WINDOW', true);
            this.$store.commit('SET_PAGE_URL', url);
        },
        showNewWindow(url) {
            window.open(url, '_blank');
        }
    }
}
</script>
<style lang="less" scoped>
    @perspectiveDir: 1003px;
    @boxHidght: 150px;

    .carousel {
        perspective: @perspectiveDir;
        -moz-perspective: @perspectiveDir;
        -ms-perspective: @perspectiveDir;
        -webkit-perspective: @perspectiveDir;
        position: relative;
        text-align: center;
        width: 100%;
        height: 100%;
        overflow: hidden;
    }
    .carousel__content {
        // display: none;
        transform-style: preserve-3d;
        position: relative;
        margin: 0 auto;
        // width: 200px;
        width: 80%;
        min-height: @boxHidght;
        height: 100%;
        transition: 1s all;
        z-index: -1;
        // border: solid green;
        display:flex;
        align-items:center;
        justify-content:center;
    }
    .box {
        // display: none;
        position: absolute;
        // border: 2px solid red;
        // width: 200px;
        width: 80%;
        min-height: @boxHidght;
        height: 80%;
        display:flex;
        align-items:center;
        justify-content:center;

        label {
            position: absolute;
            padding: 10pt;
            background-color:rgba(255, 255, 255, 0.6);
            top: 5%;
            color: #0b263d;
            font-weight: bold;
        }
        img {
            width: 100%;
            height: auto;
            // max-height: 80%;
        }
    }
    a,
    a:hover {
        color: #fff;
    }
    .carousel-btn-group {
        position: absolute;
        width: 20vw;
        max-width: 120pt;
        text-align: center;
        left: 0;
        transform: translateY(-20pt);
    }
    .btn_page {
        display: inline-block;
        border: solid 1pt #fff;
        background: rgba(29, 138, 192, 0.7);
        width: 100%;
        color: #fff;
        margin-bottom: 5pt;
        font-weight: bolder;
        letter-spacing: .2pt;
        padding-right: 2pt;
        padding-left: 2pt;

        // p::after {

        //     content: '';
        //     width: 10px;
        //     height: 10px;
        //     border: transparent 10px solid;
        //     border-right: #ff0000 10px solid;
        //     border-bottom: #ff00ff 10px solid;
        //     border-top: #00ff00 10px solid;
        //     border-left: #fff 10px solid;
        //     // width: 10px; 
        //     // height: 10px; 
        //     // border-top: 5px solid #ffffff; 
        //     // border-right: 5px solid #ffffff; 
        //     // transform: rotate(45deg);
        // }
    }
    .box:nth-child(1) {
        transform: rotateY(360deg) translateZ(160px);
        // background-color: rgba(0, 255, 255, 0.8);
    }
    .box:nth-child(2) {
        // display: none;
        // transform: rotateY(240deg) translateZ(120px);
        transform: rotateY(270deg) translateZ(160px);
        // background-color: rgba(255, 255, 0, 0.8);
    }
    .box:nth-child(3) {
        // display: none;
        // transform: rotateY(120deg) translateZ(120px);
        transform: rotateY(180deg) translateZ(160px);
        // background-color: rgba(255, 0, 255, 0.8);
    }
    .box:nth-child(4) {
        // display: none;
        // transform: rotateY(120deg) translateZ(120px);
        transform: rotateY(90deg) translateZ(160px);
        // background-color: rgba(255, 0, 255, 0.8);
    }
    .arrow {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
    }    
    .arrow--right {
        right: -15px;
        transition: all 1s;
        animation: jump 1s ease infinite;
        border: transparent 30px solid;
        border-left: #fff 30px solid;
    }
    .arrow--left {
        left: -15px;
        transition: all 1s;
        animation: jump2 1s ease infinite;
        border: transparent 30px solid;
        border-right: #fff 30px solid;
    }
    .arrowBlock {
        width: 60px;
        height: 25%;
        position: absolute;
        top: 0;
        border: solid 1px #fff;
        border-radius: 10px;
    }
    .arrowBlock--right {
        right: 10%;
        top: 50%;
        transform: translateY(-50%);
        background: linear-gradient(
            -90deg, 
            rgba(0, 0, 0, .2), 
            rgba(255, 255, 255, .3)
        );
    }
    .arrowBlock--left {
        left: 10%;
        top: 50%;
        transform: translateY(-50%);
        background: linear-gradient(
            90deg, 
            rgba(0, 0, 0, .2), 
            rgba(255, 255, 255, .3)
        );
    }
   
    @keyframes jump {
        0% {
            right: -15px;
        }
        20% {
            right: -25px;
        }
        40% {
            right: -15px;
        }
        60% {
            right: -25px;
        }
        80% {
            right: -15px;
        }
        100% {
            right: -15px;
        }
    }

    @keyframes jump2 {
        0% {
            left: -15px;
        }
        20% {
            left: -25px;
        }
        40% {
            left: -15px;
        }
        60% {
            left: -25px;
        }
        80% {
            left: -15px;
        }
        100% {
            left: -15px;
        }
    }

    @media (max-width: 580px) {
        .arrowBlock--right {
            right: 1%;
        }
        .arrowBlock--left {
            left: 1%;
        }
    }

    @media (min-width: 514px) {
        .carousel__content {
            width: 65%;
        }
        .box:nth-child(1) {
        transform: rotateY(360deg) translateZ(170px);
        }
        .box:nth-child(2) {
            transform: rotateY(270deg) translateZ(170px);
        }
        .box:nth-child(3) {
            transform: rotateY(180deg) translateZ(170px);
        }
        .box:nth-child(4) {
            transform: rotateY(90deg) translateZ(170px);
        }
    }
    @media (min-width: 654px) {
        .carousel__content {
            width: 60%;
        }
        .box:nth-child(1) {
        transform: rotateY(360deg) translateZ(190px);
        }
        .box:nth-child(2) {
            transform: rotateY(270deg) translateZ(190px);
        }
        .box:nth-child(3) {
            transform: rotateY(180deg) translateZ(190px);
        }
        .box:nth-child(4) {
            transform: rotateY(90deg) translateZ(190px);
        }
    }
    @media (min-width: 735px) {
        .box:nth-child(1) {
            transform: rotateY(360deg) translateZ(310px);
        }
        .box:nth-child(2) {
            transform: rotateY(270deg) translateZ(310px);
        }
        .box:nth-child(3) {
            transform: rotateY(180deg) translateZ(310px);
        }
        .box:nth-child(4) {
            transform: rotateY(90deg) translateZ(310px);
        }
    }
        
    @media (min-width: 946px) {
        .carousel__content {
            width: 40%;
        }
        .box:nth-child(1) {
            transform: rotateY(360deg) translateZ(250px);
        }
        .box:nth-child(2) {
            transform: rotateY(270deg) translateZ(250px);
        }
        .box:nth-child(3) {
            transform: rotateY(180deg) translateZ(250px);
        }
        .box:nth-child(4) {
            transform: rotateY(90deg) translateZ(250px);
        }
    }
</style>