<template>
    <div id="hero" :style="{ right: rightTo+'%' }">
        <span :class="animClass"></span>
    </div>
</template>
<script>
//TODO: 新增狀態 向右跑 轉正 顯示對話框

export default {
    props: {
        rightTo: {
            type: [Number, String],
            default: 10
        },
        anim: {
            type: String,
            default: 'run'
        },
    },
    data() {
        return {
            animClass: ''
        };
    },
    methods: {
        playAnim() {
            this.animClass = 'anim-run';
            setTimeout(() => {
                this.animClass = 'anim-jump';
            }, 7000);
        }
    }
}
</script>
<style lang="less" scoped>
    #hero {
        position: absolute;  
        bottom: 14.3%;
        right: 10%;
        width: 75px;
        height: 80px;
        text-align:center;
        line-height: 64px;
        transition: 7s linear all;
        z-index: 1;

        span {
            display: inline-block;
            vertical-align: middle;
            position: relative;
            width: 30px;
            height: 32px;
            transform: scale(2.5);
            background: url('../../public/images/org_a01.png');
            background-position: 60px 0px;
        }
    }
    .anim-run {
        animation-name: run;
        animation-duration: .5s;
        animation-timing-function: steps(3);
        animation-iteration-count: infinite;
    }
    .anim-jump {
        animation-name: jump;
        animation-duration: .5s;
        animation-timing-function: ease-out;
        animation-iteration-count: 2;
    }

    @keyframes jump {
        0% {
            bottom: 0%;
        }
        20% {
            bottom: 15%;
        }
        40% {
            bottom: 0%;
        }
        60% {
            bottom: 15%;
        }
        80% {
            bottom: 0%;
        }
        100% {
            bottom: 0%;
        }
    }
    @keyframes run {
        0% {
            background-position: 0px 64px;
        }
        100% {
            background-position: 90px 64px;
        }
    }
</style>