import App from '@/App.vue';
import Index from '_pages/index/index.vue';
import Products from '_pages/products/products.vue';

export default [
    {
        path: '/',
        component: App,
        children: [
            {
                path: '',
                redirect: '/index'
            },
            {
                path: '/index',
                component: Index
            },
            {
                path: '/products',
                component: Products
            }
        ]
    }
];