<template>
    <div id="loading" v-show="loading">
        <div class="wapper">
            <div v-for="n in circleNum" :key="'c_'+n" class="circle">
                <span></span>
                <span></span>
            </div>
            <div v-for="n in circleNum" :key="'s_'+n" class="shadow"></div>
            <p>Loading....</p>
        </div>
    </div>
</template>
<script>
import { mapGetters } from 'vuex';

export default {
    computed: {
        ...mapGetters(['loading'])
    },
    data() {
        return {
            circleNum: 3
        }
    }
}
</script>
<style lang="less" scoped>
    #loading {
        position: absolute;
        left: 0;
        top: 0;
        width: 100vw;
        height: 100vh;
        background: radial-gradient(#3498db, #2980b9);
        z-index: 9999;
    }
    .wapper {
        width: 200px;
        height: 60px;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);

        p {
            font-size: 20px;
            letter-spacing: 11px;
            position: absolute;
            top: 75px;
        }
    }
    .circle {
        width: 25px;
        height: 25px;
        border-radius: 50%;
        position: absolute;
        left: 15%;
        transform-origin: 50%;
        background-color: #fff;
        animation: jump .5s alternate infinite ease;

        span {
            border-radius: 50%;
            background-color: #34495e;
            display: inline-block;
            width: 8px;
            height: 8px;
            position: absolute;
            left: 0;
            animation: eye .5s alternate infinite ease;
        }
        span:nth-child(2) {
            left: auto;
            right: 0;
        }
    }
    .circle:nth-child(2) {
        left: 45%;
        animation-delay: .2s;

        span {
            animation-delay: .2s;
        }
    }
    .circle:nth-child(3) {
        left: auto;
        right: 15%;
        animation-delay: .3s;

        span {
            animation-delay: .3s;
        }
    }
    .shadow {
        width: 25px;
        height: 4px;
        border-radius: 50%;
        background-color: rgba(0, 0, 0, .5);
        position: absolute;
        transform-origin: 50%;
        top: 62px;
        left: 15%;
        z-index: -1;
        filter: blur(1px);
        animation: shadow .5s alternate infinite ease;
    }
    .shadow:nth-child(5) {
        left: 45%;
        animation-delay: .2s;
    }
    .shadow:nth-child(6) {
        left: auto;
        right: 15%;
        animation-delay: .3s;
    }
    

    @keyframes jump {
        0% {
            top: 60px;
            height: 5px;
            border-radius: 50px 50px 25px 25px;
            transform: scaleX(1.7);
        }
        40% {
            top: 20px;
            border-radius: 50%;
            transform: scaleX(1);
        }
        100% {
            top: 0%;
        }
    }
    @keyframes eye {
        0% {
            height: 2px;
        }
        40% {
            height: 5px;
        }
        100% {
            height: 10px;
        }
    }
    @keyframes shadow {
        0% {
            transform: scaleX(1.5);
        }
        40% {
            transform: scaleX(1);
            opacity: .7;
        }
        100% {
            transform: scaleX(.2);
            opacity: .4;
        }
    }
</style>