import carousel from '_components/carousel/carousel.vue';
import floor from '_components/floor.vue';
import hero from '_components/hero.vue';
import pageWindow from '_components/pageWindow.vue';

export default {
    name: 'products',
    data() {
        return {
            heroRightTo: 100,
            resTitle: '資料來源'
        }
    },
    components: {
        carousel,
        floor,
        hero,
        pageWindow
    },
    methods: {
        init() {
            this.moveHeroRightToLeft();
            this.$refs.hero.playAnim();
        },
        goHome() {
            console.log(11111, this.$router);
            this.$router.push('/');
        },
        moveHeroRightToLeft() {
            this.heroRightTo = 10;
        },
        onDataRes() {
            let htmlStr = '<h5><b>資料來源</b></h5>';
            htmlStr += '<br />';
            htmlStr += '<section class="text-left">';
            htmlStr += '<h6>素材資料來源:</h6>';
            htmlStr += '<p>http://makapri.web.fc2.com/top.html </p>';
            htmlStr += '<h6>背景圖片資料來源:</h6>';
            htmlStr += '<p>https://indienova.com/resource/r/xcfgdtkjbjcountry-platform-tiles</p>';
            htmlStr += '</section>';

            this.$store.commit('SET_ALERT_HTML', htmlStr);
            this.$store.commit('SHOW_ALERT', true);
        }
    },
    create() {
        this.$store.commit('SHOW_LOADING', false);
    },
    mounted() {
        setTimeout(() => {
            this.$store.commit('SHOW_LOADING', false);
            this.init();
        }, 2000);
    }

}