<template>
    <div id="window" @click.self="onClose" v-show="showPageWindow">
        <div class="content">
            <iframe :src="pageUrl" name="iframe"></iframe>
        </div>        
        <button class="btn btn-primary btn-close" @click.stop="onClose">X</button>
    </div>
</template>
<script>
import { mapGetters } from 'vuex';

export default {
    computed: {
        ...mapGetters(['showPageWindow', 'pageUrl'])
    },
    methods: {
        onClose() {
            event.preventDefault();
            this.$store.commit('SHOW_PAGE_WINDOW', false);
            this.$store.commit('SET_PAGE_URL', '');
        }
    }
}
</script>
<style lang="less" scoped>
    #window {
        width: 100vw;
        height: 100vh;
        position: absolute;
        top: 0;
        left: 0;
        background-color: rgba(255, 255, 255, .8);
        z-index: 9999;

        &::before {
            content:'';
            width:0;
            height:100%;
            display:inline-block;
            position:relative;
            vertical-align:middle;
        } 
    }
    .content {
        width: 80vw;
        height: 80vh;
        display:inline-block;
        vertical-align:middle;
    }
    iframe {
        width: 100%;
        height: 100%;
        border: solid 0px;
    }
    .btn-close {
        height: 40pt;
        width: 40pt;
        position: absolute;
        top: 2%;
        right: 4%;
        font-weight: bold;
        font-size: 20pt
    }
</style>